import React, { Fragment } from 'react';
import APILayout from '../../../../layouts/APILayout';
import Section from '../../../../modules/apidoc/components/Section';
import FunctionName from '../../../../modules/apidoc/components/FunctionName';
import ParameterTable from '../../../../modules/apidoc/components/ParameterTable';
import Parameter from '../../../../modules/apidoc/components/Parameter';
import { MapType, TileLayerType, ProjectionType, FeatureType, CoordinateType, ExtentType } from '../../../../modules/apidoc/components/OpenlayersDataTypes';
import ParameterGroup from '../../../../modules/apidoc/components/ParameterGroup';
import Highlighted from '../../../../modules/apidoc/components/Highlighted';
import FunctionDescription from '../../../../modules/apidoc/components/FunctionDescription';
import Returns from '../../../../modules/apidoc/components/Returns';
import Link from '../../../../modules/apidoc/components/Link';

const ECQLLink = () => <Highlighted><Link href='https://docs.geoserver.org/stable/en/user/tutorials/cql/cql_tutorial.html'>ECQL</Link></Highlighted>;
const GeoserverWFSGetFeatureOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wfs/reference.html#getfeature">https://docs.geoserver.org/stable/en/user/services/wfs/reference.html#getfeature</Link>;
const GeoserverWMSGetMapOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getmap">https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getmap</Link>;
const GeoserverWMSGetFeatureInfoOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getfeatureinfo">https://docs.geoserver.org/stable/en/user/services/wms/reference.html#getfeatureinfo</Link>;
const GeoserverWMSGetLegendGraphicOperationDocLink = () => <Link href="https://docs.geoserver.org/stable/en/user/services/wms/get_legend_graphic/index.html">https://docs.geoserver.org/stable/en/user/services/wms/get_legend_graphic/index.html</Link>;

const WFSGetFeatureParamsParameter = () => <Parameter name='getFeatureParams' type='object'>object containing WFS GetFeature operation parameters. See <GeoserverWFSGetFeatureOperationDocLink /></Parameter>;
const WMSGetLegendGraphicParamsParameter = () => <Parameter name='getLegendGraphicParams' type='object'>object containing WMS GetLegendGraphic operation parameters. See <GeoserverWMSGetLegendGraphicOperationDocLink /></Parameter>;
const ECQLFilterParameter = () => <Parameter name='cql_filter' type='string'><Highlighted>ECQL</Highlighted> filter expression.</Parameter>;

const FormatOptionsParameter = () => <Fragment>
    <ParameterGroup name='format' type='feature format Options'>
        <ParameterTable>
            <Parameter name='dataProjection' type={<ProjectionType />}>
                Projection of the data we are reading. If not provided, the projection will be derived from the data (where possible) or the <Highlighted>dataProjection</Highlighted> of the format is assigned (where set). If the projection can not be derived from the data and if no <Highlighted>dataProjection</Highlighted> is set for a format, the features will not be reprojected.
                        </Parameter>
            <Parameter name='featureProjection' type={<ProjectionType />}>Projection of the feature geometries created by the format reader. If not provided, features will be returned in the <Highlighted>dataProjection</Highlighted>.</Parameter>
        </ParameterTable>
    </ParameterGroup>
</Fragment>

export default () => (
    <APILayout>
        <h4 className='font-weight-bold mb-4'>easyolmaps/layer/GeoserverLayer</h4>
        <Section>
            <FunctionName parameters={['layerName', 'options']}>new GeoserverLayer</FunctionName>
            <ParameterTable>
                <Parameter name='layerName' type='string'>Layer name.</Parameter>
                <ParameterGroup name='options' type='layer Options'>
                    <ParameterTable withDefaultColumn={true}>
                        <Parameter name='visible' type='boolean' defaultValue='true'>visibility</Parameter>
                        <Parameter name='url' type='string' defaultValue=' '>WMS service URL</Parameter>
                        <Parameter name='crossOrigin' type='boolean' defaultValue=' '>
                            The crossOrigin attribute for loaded images. Note that you must provide a crossOrigin value if you are using the WebGL renderer or if you want to access pixel data with the Canvas renderer. See <Link href='https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image'>https://developer.mozilla.org/en-US/docs/Web/HTML/CORS_enabled_image</Link> for more detail.
                        </Parameter>
                        <Parameter name='getMapParams' type='object' defaultValue=' '>
                            object containing WMS GetMap operation parameters. See <GeoserverWMSGetMapOperationDocLink />
                        </Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <strong>Extends:</strong>
            <ul>
                <li><TileLayerType /></li>
            </ul>
        </Section>
        <Section>
            <FunctionName>refresh</FunctionName>
            <FunctionDescription>Refresh the layer.</FunctionDescription>
        </Section>
        <Section>
            <FunctionName parameters={['cql_filter']}>filter</FunctionName>
            <FunctionDescription>Apply a filter to the layer using <ECQLLink /></FunctionDescription>
            <ParameterTable>
                <ECQLFilterParameter />
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='String'>getFilter</FunctionName>
            <FunctionDescription>Return the current filter applied to the layer</FunctionDescription>
            <Returns><Highlighted>ECQL</Highlighted> filter</Returns>
        </Section>
        <Section>
            <FunctionName parameters={['style']}>setStyle</FunctionName>
            <FunctionDescription>Set the style for the layer</FunctionDescription>
            <ParameterTable>
                <Parameter name='style' type='string'>Name of the style to use. It may be empty to use the default layer styling.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='String'>getStyle</FunctionName>
            <FunctionDescription>Get the current style of the layer. An empty string means the layer is using its default style</FunctionDescription>
            <Returns>Name of the current style</Returns>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['coordinate', 'map', 'getFeatureInfoParams', 'format']}>getFeaturesAtCoordinate</FunctionName>
            <FunctionDescription>Get all features whose geometry intersects the provided coordinate.</FunctionDescription>
            <ParameterTable>
                <Parameter name='coordinate' type={<CoordinateType />}>Coordinate.</Parameter>
                <Parameter name='map' type={<MapType />}>The Map.</Parameter>
                <Parameter name='getFeatureInfoParams' type='object'>object containing WMS GetFeatureInfo operation parameters. See <GeoserverWMSGetFeatureInfoOperationDocLink /> </Parameter>
                <FormatOptionsParameter />
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='features' type={<FeatureType array />}>Features.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='IMG' parameters={['getLegendGraphicParams']}>getLegendGraphic</FunctionName>
            <FunctionDescription>Get legend of the layer.</FunctionDescription>
            <ParameterTable>
                <WMSGetLegendGraphicParamsParameter />
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['getFeatureParams']}>countFeatures</FunctionName>
            <FunctionDescription>Get the number of features of the layer.</FunctionDescription>
            <ParameterTable>
                <WFSGetFeatureParamsParameter />
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='numberOfFeatures' type='number'>Number of features</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['getFeatureParams', 'format']}>getFeatures</FunctionName>
            <FunctionDescription>Get features of the layer using the WFS GetFeature operation.</FunctionDescription>
            <ParameterTable>
                <WFSGetFeatureParamsParameter />
                <FormatOptionsParameter />
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='features' type={<FeatureType array />}>Features.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' comment={<span className='ml-2 bg-warning'>
                ¡Introduced in replace of <strong>getAttributes</strong>!
            </span>}>getFeatureTypeDescription</FunctionName>
            <FunctionDescription>Get the description of the layer feature type.</FunctionDescription>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='featureTypeDescription' type='object'>Feature type description.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName parameters={['getFeatureParams']} returns='URL'> exportFeatures</FunctionName>
            <FunctionDescription>Get URL for exporting features.</FunctionDescription>
            <ParameterTable>
                <WFSGetFeatureParamsParameter />
            </ParameterTable>
            <Returns>Features exported in the specified format</Returns>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['cql_filter', 'transform']}>getBBOX</FunctionName>
            <FunctionDescription>Get the bbox of the layer. It takes into account the <Highlighted>cql_filter</Highlighted> parameter to resolve the extent.</FunctionDescription>
            <ParameterTable>
                <ParameterGroup name='options' type='getBBOX Options'>
                    <ParameterTable>
                        <ECQLFilterParameter />
                        <ParameterGroup name='transform' type='transform Options'>
                            <ParameterTable>
                                <Parameter name='source' type={<ProjectionType />}>Source projection-like.</Parameter>
                                <Parameter name='destination' type={<ProjectionType />}>Destination projection-like.</Parameter>
                            </ParameterTable>
                        </ParameterGroup>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='extent' type={<ExtentType />}>BBOX of the layer.</Parameter>
            </ParameterTable>
        </Section>
        <Section>
            <FunctionName returns='Promise' parameters={['options']} comment={<span className='ml-2 bg-warning'>
                ¡Introduced in replace of <strong>groupBy</strong>!
            </span>}>aggregate</FunctionName>
            <FunctionDescription>Perform aggregation features on the features of the layers</FunctionDescription>
            <ParameterTable>
                <ParameterGroup name='options' type='aggregate Options'>
                    <ParameterTable>
                        <ECQLFilterParameter />
                        <Parameter name='aggregationAttribute' type='string'>Attribute on which to perform aggregation</Parameter>
                        <Parameter name='functions' type='Array.<string>'>Aggregate functions to compute. Functions include <Highlighted>Count</Highlighted>, <Highlighted>Average</Highlighted>, <Highlighted>Max</Highlighted>, <Highlighted>Median</Highlighted>, <Highlighted>Min</Highlighted>, <Highlighted>StdDev</Highlighted>, and <Highlighted>Sum</Highlighted>.</Parameter>
                        <Parameter name='groupByAttributes' type='Array.<string>'>List of group by attributes.</Parameter>
                    </ParameterTable>
                </ParameterGroup>
            </ParameterTable>
            <Returns>Promise</Returns>
            <ParameterTable>
                <Parameter name='aggregationResult' type='object'>Result of the aggregations.</Parameter>
            </ParameterTable>
        </Section>
    </APILayout >
);